.main_container{
  position: absolute;
  padding: 70px 140px 0 140px;
}

.main_text{
  position: absolute;
  bottom: 18px;
  right: 140px;

  font-family: Montserrat-Regular, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  /* or 171% */

  text-align: right;

  /* Dark 75% */

  color: rgba(31, 32, 65, 0.75);
}

.search_room{
  width: 320px;
  height: 374px;
  padding: 40px 30px 0 30px;

  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
  background: white;
}

.search_room h2{
  font-family: Tahoma, sans-serif;
  margin: 0;
}

.search_room h3{
  font-family: Montserrat-Regular, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #1F2041;

  margin: 0;
}

.search_room div{
  font-family: Montserrat-Regular, sans-serif;
  margin-top: 20px;
}

.date{
  display: flex;
  flex-direction: row;
}

.date.arrive,
.date.leave{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.confirm{
  display: flex;
  flex-direction: row;
  align-items: center;

  font-family: Montserrat-Regular, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  text-transform: uppercase;
  color: white;

  width: 320px;
  height: 44px;
  padding-left: 42px;
  margin: 30px 0;

  border: 0;
  border-radius: 22px;
  background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
}

.confirm>img{
  height: 16px;
  width: 16px;
  margin-left: 10px;
}

.confirm>p{
  width: 236px;
}